/* Timetable.css */

/* Container styling */
.timetable-container {
  margin: 20px auto;
  padding: 20px;
  width: 80%;
  max-width: 800px;
  background-color: #f8f9fa;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  font-family: 'Arial', sans-serif;
}

/* Title styling */
h2 {
  text-align: center;
  font-size: 24px;
  color: #343a40;
  margin-bottom: 20px;
  font-weight: bold;
}

/* Dropdown styling */
select {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #ced4da;
  background-color: #fff;
  color: #495057;
  transition: border-color 0.3s ease;
}

select:focus {
  border-color: #007bff;
  outline: none;
}

/* Button styling */
button {
  display: block;
  width: 100%;
  padding: 12px;
  background-color: #28a745;
  color: #fff;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: 20px;
}

button:hover {
  background-color: #218838;
}

/* Timetable info styling */
h3 {
  margin-top: 20px;
  font-size: 20px;
  color: #007bff;
  text-align: center;
}

p {
  font-size: 16px;
  color: #495057;
  padding: 8px 0;
  border-bottom: 1px solid #dee2e6;
}

/* Full day timetable styling */
h4 {
  margin-top: 20px;
  font-size: 18px;
  color: #343a40;
  padding: 10px 0;
  border-bottom: 1px solid #e9ecef;
}

/* Add hover effects to the timetable rows */
p:hover {
  background-color: #f1f3f5;
  cursor: pointer;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .timetable-container {
    width: 95%;
  }

  select, button {
    font-size: 14px;
    padding: 8px;
  }

  p {
    font-size: 14px;
  }

  h3, h4 {
    font-size: 18px;
  }
}
