.faculty-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 250px;
    padding: 20px;
    margin: 15px;
    background-color: #ffffff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    border-radius: 12px;
    transition: transform 0.3s ease;
  }
  
  .faculty-card:hover {
    transform: translateY(-5px);
  }
  
  .faculty-image {
    width: 100%;
    height: 250px;
    object-fit: cover;
    border-radius: 8px;
  }
  
  .faculty-info {
    margin-top: 15px;
    text-align: center;
    font-family: Arial, sans-serif;
    color: #333;
  }
  
  .faculty-name {
    font-size: 1.2em;
    font-weight: bold;
    color: #1a73e8;
  }
  
  .faculty-designation {
    font-size: 1em;
    color: #555;
  }
  
  .faculty-contact {
    font-size: 0.9em;
    color: #666;
    margin-top: 8px;
  }
  
  .faculty-contact a {
    color: #1a73e8;
    text-decoration: none;
  }
  
  .faculty-contact a:hover {
    text-decoration: underline;
  }
  .download-button {
    margin: 20px auto;
    padding: 10px 20px;
    font-size: 16px;
    color: #fff;
    background-color: #1a73e8;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .download-button:hover {
    background-color: #0c57b5;
  }  