.course-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    font-family: Arial, sans-serif;
  }
  
  .course-container h1 {
    font-size: 2em;
    margin-bottom: 20px;
    color: #333;
  }
  
  .course-list {
    display: flex;
    flex-direction: column;
    gap: 15px;
    width: 100%;
    max-width: 400px; /* Adjusted for mobile view */
  }
  
  .course-card {
    background-color: #f8f9fa;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 15px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
    transition: transform 0.2s; /* Smooth transition for hover effect */
  }
  
  .course-card:hover {
    transform: scale(1.02); /* Slightly enlarge on hover */
  }
  
  .course-branch {
    font-size: 1.5em;
    color: #007bff;
    margin: 0;
  }
  
  .course-capacity {
    font-size: 1.2em;
    color: #555;
    margin: 5px 0 0;
  }
  