.notice-container {
    display: flex;
    flex-direction: column; /* Vertical stacking of cards */
    align-items: center;
    padding: 20px;
    gap: 16px;
  }
  
  .notice-card {
    display: flex;
    flex-direction: column;
    width: 90%;
    max-width: 500px;
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    background-color: #fff;
    transition: transform 0.2s;
  }
  
  .notice-card:hover {
    transform: translateY(-5px);
  }
  
  .notice-image {
    width: 100%;
    height: auto;
    object-fit: cover;
    cursor: pointer;
    transition: transform 0.3s;
  }
  
  .notice-image:hover {
    transform: scale(1.05);
  }
  
  .notice-details {
    padding: 16px;
    text-align: center;
  }
  
  .notice-date {
    font-size: 0.9em;
    color: #555;
    background-color: #fff8dc;
    padding: 4px 8px;
    border-radius: 4px;
    margin-bottom: 8px;
  }
  
  .notice-text {
    font-size: 1em;
    color: #333;
    margin-bottom: 12px;
  }
  
  .pdf-button {
    padding: 8px 12px;
    font-size: 0.9em;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .pdf-button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }
  
  .pdf-button:not(:disabled):hover {
    background-color: #0056b3;
  }
  